@charset "UTF-8";
*,
*:before,
*:after {
  box-sizing: border-box; }

html,
body,
#root {
  height: 100%; }

html {
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  background-color: #193e2d;
  color: #ffffff;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

h1,
p,
ul {
  margin: 0;
  padding: 0; }

ul,
li {
  list-style: none; }

a {
  color: inherit; }
  a[target='_blank']::after {
    content: ' â†—'; }

table {
  border-collapse: collapse; }

button {
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  cursor: pointer;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit; }
  button:disabled {
    cursor: default; }
